body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.capitalize {
  text-transform: capitalize;
}

.deleteBtn {
  font-size: 12px;
  background-color: #fff;
  border: 1px solid lightgray;
  -webkit-box-shadow: none;
          box-shadow: none;
  height: 25px;
  -ms-flex-item-align: center;
      align-self: center;
  margin-left: 5px;
  border-radius: 5px;
}

.notification {
  border: 2px solid green;
  color: green;
  font-size: 20px;
  background-color: lightgrey;
  padding: .5em;
  margin: .5em;
}

.error {
  border: 2px solid red;
  color: red;
  font-size: 20px;
  background-color: lightgrey;
  padding: .5em;
  margin: .5em;
}


